<template>
  <v-card class="mx-auto">
    <v-card-title>
      <v-row justify="space-between">
        <v-col cols="12" sm="4">
          История игр
        </v-col>
        <v-col cols="12" sm="4">
          <v-select
            label="Категория" 
            :items="groups"
            v-model="group_type"
          ></v-select>
        </v-col>
        <v-col cols="12" sm="4">
          <v-text-field
            v-model="search"
            append-icon="mdi-magnify"
            label="Поиск"
          ></v-text-field>
        </v-col>
      </v-row>
    </v-card-title>
    <v-data-table
      :headers="headers"
      :items="filteredItems"
      :search="search"
    >
      <template v-slot:item.gameId="props">
        <v-edit-dialog
          :return-value.sync="props.item.gameId"
          large
          persistent
          @save="save"
          @cancel="cancel"
          @open="open"
          @close="close"
        >
          <span>{{ props.item.gameId }}</span>
          <template v-slot:input>
            <v-text-field
              v-model="props.item.gameId"
              label="Edit"
              single-line
              counter
              autofocus
            ></v-text-field>
          </template>
        </v-edit-dialog>
      </template>
      <template v-slot:item.date="props">
        <v-edit-dialog
          :return-value.sync="props.item.date"
          large
          persistent
          @save="save"
          @cancel="cancel"
          @open="open"
          @close="close"
        >
          <span>{{ props.item.date }}</span>
          <template v-slot:input>
            <v-text-field
              v-model="props.item.date"
              label="Edit"
              single-line
              counter
              autofocus
            ></v-text-field>
          </template>
        </v-edit-dialog>
      </template>
      <template v-slot:item.winnerId="props">
        <v-edit-dialog
          :return-value.sync="props.item.winnerId"
          large
          persistent
          @save="save"
          @cancel="cancel"
          @open="open"
          @close="close"
        >
          <span>{{ props.item.winnerId }}</span>
          <template v-slot:input>
            <v-text-field
              v-model="props.item.winnerId"
              label="Edit"
              single-line
              counter
              autofocus
            ></v-text-field>
          </template>
        </v-edit-dialog>
      </template>
      <template v-slot:item.winnerName="props">
        <v-edit-dialog
          :return-value.sync="props.item.winnerName"
          large
          persistent
          @save="save"
          @cancel="cancel"
          @open="open"
          @close="close"
        >
          <span>{{ props.item.winnerName }}</span>
          <template v-slot:input>
            <v-text-field
              v-model="props.item.winnerName"
              label="Edit"
              single-line
              counter
              autofocus
            ></v-text-field>
          </template>
        </v-edit-dialog>
      </template>
      <template v-slot:item.chanceOfVictory="props">
        <v-edit-dialog
          :return-value.sync="props.item.chanceOfVictory"
          large
          persistent
          @save="save"
          @cancel="cancel"
          @open="open"
          @close="close"
        >
          <span>{{ props.item.chanceOfVictory }}</span>
          <template v-slot:input>
            <v-text-field
              v-model="props.item.chanceOfVictory"
              label="Edit"
              single-line
              counter
              autofocus
            ></v-text-field>
          </template>
        </v-edit-dialog>
      </template>
      <template v-slot:item.action>
        <v-btn icon>
          <v-icon small>
            mdi-close-circle-outline
          </v-icon>
        </v-btn>
      </template> 
    </v-data-table>

    <v-snackbar 
      v-model="snack" 
      :timeout="3000" 
      :color="snackColor"
    >
      {{ snackText }}
      <v-btn 
        text 
        @click="snack = false"
      >
        Close
      </v-btn>
    </v-snackbar>
  </v-card>
</template>

<script>
  export default {
    data: () => ({
      search: '',
      snack: false,
      snackColor: '',
      snackText: '',
      groups: ['All', 'Done', 'Process', 'Error'],
      group_type: null,
      headers: [
        { text: 'ID игры', align: 'left', value: 'gameId' },
        { text: 'Дата', value: 'date' },
        { text: 'ID победителя', value: 'winnerId' },
        { text: 'Имя победителя', value: 'winnerName' },
        { text: 'Шанс победы', value: 'chanceOfVictory' },
        { text: 'Статус', value: 'status' },
        { text: 'Действия', value: 'action', sortable: false }
      ],
      data: [
        { 
          gameId: 1,
          date: new Date().toISOString().substr(0, 10),
          winnerId: 12,
          winnerName: 'Саша',
          chanceOfVictory: 0.42,
          status: 'done'
        },
        { 
          gameId: 2,
          date: new Date().toISOString().substr(0, 10),
          winnerId: 2,
          winnerName: 'Ваня',
          chanceOfVictory: 0.61,
          status: 'done'      
        },
        { 
          gameId: 3,
          date: new Date().toISOString().substr(0, 10),
          winnerId: 43,
          winnerName: 'Катя',
          chanceOfVictory: 0.92,
          status: 'process'
        },
        { 
          gameId: 4,
          date: new Date().toISOString().substr(0, 10),
          winnerId: 3,
          winnerName: 'Даша',
          chanceOfVictory: 0.01,
          status: 'done' 
        },
        { 
          gameId: 5,
          date: new Date().toISOString().substr(0, 10),
          winnerId: 6,
          winnerName: 'Леша',
          chanceOfVictory: 0.94,
          status: 'done' 
        },
        { 
          gameId: 6,
          date: new Date().toISOString().substr(0, 10),
          winnerId: 24,
          winnerName: 'Андрей',
          chanceOfVictory: 0.25,
          status: 'process'
        }          
      ]
    }),
    methods: {
      save () {
        this.snack = true;
        this.snackColor = 'success';
        this.snackText = 'Данные обновлены';
      },
      cancel () {
        this.snack = true;
        this.snackColor = 'error';
        this.snackText = 'Отмена';
      },
      open () {
        this.snack = true;
        this.snackColor = 'info';
        this.snackText = 'Редактирование';
      },
      close () {
        console.log('Dialog closed');
      }
    },
    computed: {
      filteredItems() {
        return this.data.filter((item) => {
          return (this.group_type == 'All') ? item : !this.group_type || (item.status.toLowerCase() === this.group_type.toLowerCase());
        });
      }
    }
  }
</script>
